/* General Body Styles */
body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f7f6; /* Light gray background */
  color: #333;
}

/* App Container */
.task-app-container {
  max-width: 1000px;
  margin: 30px auto;
  padding: 25px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
}

/* Header */
.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  padding-bottom: 15px;
  border-bottom: 1px solid #eee;
}

.app-header h1 {
  color: #2c3e50; /* Dark blue-gray for headings */
  margin: 0;
  font-size: 2.2em;
  font-weight: 600;
}

/* Search Input */
.search-input {
  padding: 10px 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1em;
  width: 250px;
  transition: border-color 0.3s ease;
}

.search-input:focus {
  border-color: #3498db; /* Blue accent on focus */
  outline: none;
}

/* Form Styles */
.task-form {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
  padding: 20px;
  background-color: #fdfdfd;
  border: 1px solid #eee;
  border-radius: 8px;
}

.task-form input[type="text"],
.task-form input[type="date"],
.task-form select,
.task-form button {
  padding: 12px 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1em;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.task-form input:focus,
.task-form select:focus {
  border-color: #3498db;
  box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.2);
  outline: none;
}

.task-form button {
  background-color: #3498db; /* Blue button */
  color: white;
  border: none;
  cursor: pointer;
  font-weight: 600;
  transition: background-color 0.3s ease;
}

.task-form button:hover {
  background-color: #2980b9; /* Darker blue on hover */
}

/* Table Styles */
.task-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
  background-color: #ffffff;
  border-radius: 8px;
  overflow: hidden; /* Ensures rounded corners apply to children */
}

.task-table th,
.task-table td {
  padding: 15px;
  text-align: left;
  border-bottom: 1px solid #eee;
}

.task-table th {
  background-color: #ecf0f1; /* Light gray for table headers */
  color: #555;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 0.9em;
}

.task-table tbody tr:hover {
  background-color: #f8f8f8;
}

/* Assignee specific styles */
.assignee-item {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  padding: 8px;
  border: 1px solid #f0f0f0;
  border-radius: 5px;
  background-color: #fafafa;
}

.assignee-item label {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
  font-weight: 500;
}

.assignee-item input[type="checkbox"] {
  margin-right: 8px;
  transform: scale(1.2);
}

.assignee-item .delete-button {
  background-color: #f44336;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.delete-button:hover {
  background-color: #da190b;
}


.assignee-item textarea:focus {
  border-color: #3498db;
  box-shadow: 0 0 0 2px rgba(52, 152, 219, 0.15);
  outline: none;
}
